import React, { PureComponent } from 'react'
import { withStyles, Theme, createStyles, WithStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography'
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import Mutation from '../components/Mutation'
import Query from '../components/Query'
import { SITE, SITE_ENTRIES } from '../graphql/query/remote'
import { ENTRY_TABLE as ENTRY_TABLE_QUERY } from '../graphql/query/local'
import { ENTRY_TABLE } from '../graphql/mutation/local'
import { DELETE_SITE_ENTRIES } from '../graphql/mutation/remote'
import { defaultState } from '../graphql/resolvers'
import commanRender, { RENDER_VALUE } from '../utils/commanRender'
import toCurrency from '../utils/toCurrency'
import { Routes } from '../Router'
import EnhancedTable from '../components/Enhanced/Table/Table'
import TableWrapper from '../components/TableWrapper'

const styles = (theme: Theme) => createStyles({
  paper: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing.unit * 2,
    paddingBottom: theme.spacing.unit * 2,
    marginTop: theme.spacing.unit * 2,
    marginBottom: theme.spacing.unit * 2,
  },
  fab: {
    position: 'fixed',
    bottom: theme.spacing.unit * 2,
    right: theme.spacing.unit * 2,
  },
})

const columnData = [
  { id: 'createdAt', numeric: false, disablePadding: true, colSpan: 1, label: 'Created At' },
  { id: 'mistri', numeric: false, disablePadding: false, colSpan: 2, label: 'MISTRI (Quantity & Price)' },
  { id: 'labour', numeric: false, disablePadding: false, colSpan: 2, label: 'LABOUR (Quantity & Price)' },
  { id: 'eit', numeric: false, disablePadding: false, colSpan: 2, label: 'EIT (Quantity & Price)' },
  { id: 'morang', numeric: false, disablePadding: false, colSpan: 2, label: 'MORANG (Quantity & Price)' },
  { id: 'baalu', numeric: false, disablePadding: false, colSpan: 2, label: 'BAALU (Quantity & Price)' },
  { id: 'githi', numeric: false, disablePadding: false, colSpan: 2, label: 'GITHI (Quantity & Price)' },
  { id: 'cement', numeric: false, disablePadding: false, colSpan: 2, label: 'CEMENT (Quantity & Price)' },
  { id: 'saria', numeric: false, disablePadding: false, colSpan: 2, label: 'SARIA (Quantity & Price)' },
  { id: 'dust', numeric: false, disablePadding: false, colSpan: 2, label: 'DUST (Quantity & Price)' },
  { id: 'other', numeric: false, disablePadding: false, colSpan: 2, label: 'OTHER (Quantity & Price)' },
  { id: 'other2', numeric: false, disablePadding: false, colSpan: 2, label: 'OTHER 2 (Quantity & Price)' },
  { id: 'managerTotal', numeric: false, disablePadding: false, colSpan: 1, label: 'MANAGER TOTAL (₹)' },
  { id: 'total', numeric: false, disablePadding: false, colSpan: 1, label: 'TOTAL (₹)' },
]
const exclude = ['id', 'count', 'site', 'createdAt', 'updatedAt', '__typename']

const totalColumnData = [
  { id: 'mistri', numeric: false, disablePadding: false, colSpan: 2, label: 'MISTRI (Quantity & Price) (₹)' },
  { id: 'labour', numeric: false, disablePadding: false, colSpan: 2, label: 'LABOUR (Quantity & Price) (₹)' },
  { id: 'eit', numeric: false, disablePadding: false, colSpan: 2, label: 'EIT (Quantity & Price) (₹)' },
  { id: 'morang', numeric: false, disablePadding: false, colSpan: 2, label: 'MORANG (Quantity & Price) (₹)' },
  { id: 'baalu', numeric: false, disablePadding: false, colSpan: 2, label: 'BAALU (Quantity & Price) (₹)' },
  { id: 'githi', numeric: false, disablePadding: false, colSpan: 2, label: 'GITHI (Quantity & Price) (₹)' },
  { id: 'cement', numeric: false, disablePadding: false, colSpan: 2, label: 'CEMENT (Quantity & Price) (₹)' },
  { id: 'saria', numeric: false, disablePadding: false, colSpan: 2, label: 'SARIA (Quantity & Price) (₹)' },
  { id: 'dust', numeric: false, disablePadding: false, colSpan: 2, label: 'DUST (Quantity & Price) (₹)' },
  { id: 'other', numeric: false, disablePadding: true, colSpan: 1, label: 'OTHER (Price) (₹)' },
  { id: 'other2', numeric: false, disablePadding: true, colSpan: 1, label: 'OTHER 2 (Price) (₹)' },
  { id: 'managerTotal', numeric: false, disablePadding: true, colSpan: 1, label: 'MANAGER TOTAL (₹)' },
  { id: 'total', numeric: false, disablePadding: true, colSpan: 1, label: 'TOTAL (₹)' },
]
const totalExclude = ['id', 'count', 'createdAt', 'updatedAt', '__typename']

interface ISiteDetailProps extends WithStyles<typeof styles> {
  match?: any,
  history?: any,
  user: any
}
const SiteDetail = withStyles(styles)((props: ISiteDetailProps) => {
  const { id } = props.match.params
  const { classes, history, user, ...restProps } = props
  const { entryTotalTable, entryTable } = defaultState
  return (
    <React.Fragment>
      <Query query={SITE} variables={{ id }}>
        {({ loading, data: { site } }: IGraphqlData) => {
          return (
            <React.Fragment>
              <Paper className={classes.paper}>
                <Grid container={true} spacing={32}>
                  <Grid item={true} container={true} direction='column' sm={4}>
                    <Grid item={true}>
                      <Typography variant='headline' className='ttc' color='primary'>
                        Name: {site.name}
                      </Typography>
                    </Grid>
                    <Grid item={true}>
                      <Typography variant='headline' className='ttc' color='secondary'>
                        Location: {site.location}
                      </Typography>
                    </Grid>
                  </Grid>
                  <Grid item={true} container={true} direction='column' sm={4}>
                    <Grid item={true}>
                      <Typography variant='subheading'>
                        Total Cost  (₹): {toCurrency(site.cost)}
                      </Typography>
                    </Grid>
                    <Grid item={true}>
                      <Typography variant='subheading'>
                        Total Entry: {toCurrency(site.entryCount)}
                      </Typography>
                    </Grid>
                  </Grid>
                  <Grid item={true} container={true} direction='column' sm={4}>
                    <Grid item={true}>
                      <Typography variant='subheading'>
                        Manager : {site.manager.name}
                      </Typography>
                    </Grid>
                    <Grid item={true}>
                      <Typography variant='subheading'>
                        Manager Spent Amount: ₹ <span style={{ color: RENDER_VALUE({ value: site.managerSpentAmount, balance: false }).color || undefined }}>{RENDER_VALUE({ value: site.managerSpentAmount }).value}</span>
                      </Typography>
                    </Grid>
                  </Grid>
                </Grid>
              </Paper>
              <EnhancedTable
                {...restProps}
                loading={loading}
                data={{ ...site.total, managerSpentAmount: site.managerSpentAmount, total: site.cost }}
                count={1}
                settings={entryTotalTable}
                title='Total'
                columnData={totalColumnData}
                exclude={totalExclude}
                render={commanRender({ match: 'managerSpentAmount' })}
                mutate={e => null}
              >
              </EnhancedTable>
              <TableWrapper
                tableMutation={ENTRY_TABLE}
                tableQuery={ENTRY_TABLE_QUERY}
                dataQuery={SITE_ENTRIES}
                defaultState={entryTable}
                getQueryVariables={() => ({ siteId: id })}
                resetOnMount={true}
              >
                {
                  (tableSettings: typeof entryTable, lmutate: Function, { loading, data, refetch }: IGraphqlData) => {
                    const siteEntries = data[Object.keys(data)[0]]
                    return (
                      <Mutation mutation={DELETE_SITE_ENTRIES}>
                        {(deleteMutate: Function, { loading: dloading }: IGraphqlData) => {
                          return (
                            <EnhancedTable
                              {...restProps}
                              loading={loading || dloading}
                              data={siteEntries}
                              count={site.entryCount}
                              settings={tableSettings}
                              title='Entry'
                              columnData={columnData}
                              exclude={exclude}
                              options={{
                                onEdit: (user && user.role === '1') ? (async (args: any) => {
                                  history.push(Routes.SiteEntry.create({ siteId: id, edit: true, id: args }))
                                }) : (undefined),
                                onDelete: (user && user.role === '1') ? (async (args: any) => {
                                  console.log('calling deleteMutate function...')
                                  await deleteMutate({ variables: { ...args, siteId: id } })
                                  await lmutate({ variables: entryTable })
                                  refetch()
                                }) : (undefined),
                                onAdd: () => history.push(Routes.SiteEntry.create({ siteId: id, edit: false, id: undefined })),
                              }}
                              render={commanRender({ match: 'managerSpentAmount' })}
                              mutate={async args => {
                                await lmutate({ variables: args })
                                // refetch();
                              }}>
                            </EnhancedTable>
                          )
                        }}
                      </Mutation>
                    )
                  }}
              </TableWrapper>
            </React.Fragment>
          )
        }}
      </Query>
    </React.Fragment>
  )
})

export default SiteDetail
